import React from 'react';
import ServicesGenerator from './ServicesGenerator/ServicesGenerator';

const Services = (props) => {
	let categories = [];
	let to_render = [];
	let content = [];
	if (props.language === 'English') content = props.contents;
	else content = props.contents_zh;
	for (let item in content) {
		categories.push(item);
	}

	console.log(content);
	
	for (let i = 0 ; i < categories.length ; i ++) {
		to_render.push(<h3>{content[categories[i]][0].display_name}</h3>);
		to_render.push(<ServicesGenerator services = {content[categories[i]]}/>);
	}

	return (
		<div>
			<div className = 'column big left extended'>

				{to_render}				

			</div>
			<div className = 'column small right static_others'>
				<img className = 'photo' src = './pics/services_sidephoto.png' alt = 'error'/>
			</div>
		</div>
	);
}


export default Services;