import React, { Component } from 'react';
import './App.css';

import TitleAndDropdown from './TitleAndDropdown/TitleAndDropdown';

//import Dropdown from './Dropdown/Dropdown';
import DropdownMobile from './Dropdown/DropdownMobile';
//import {DataImport} from './DataImport/DataImport';

import Profile from './Profile/Profile';
import Publications from './Publications/Publications';
import Projects from './Projects/Projects';
import Team from './Team/Team';
import Services from './Services/Services';

let profile_intro_test = [];

class App extends Component {

  state = {
    language: 'English',

    Profile: false, 
    Publications: false,
    Projects: false,
    Team: false,
    Services: false,
    mobile: false,

    current_page: 'Profile',
    
    profile_content: {},
    publications_content: {},
    projects_content: {},
    team_content: {},
    services_content: {},

    profile_zh_content: {},
    publications_zh_content: {},
    projects_zh_content: {},
    team_zh_content: {},
    services_zh_content: {}
  }

  componentDidMount () {

    var gsjson = require('google-spreadsheet-to-json');
      gsjson({
        spreadsheetId: '10RbLqqMM_hU9wsB4ROexwjlbxytBj_bgqsJ7ZlpgGYY',
        worksheet: ['profile', 'publications', 'projects', 'team', 'services', 
        'profile_zh', 'publications_zh', 'projects_zh', 'team_zh', 'services_zh']
      })
      .then( (contents) => {

        document.getElementById('Profile').classList.add('navbar-click');
      //profile
        const profile_content = contents[0];
        let profile = {};
        for (let i = 0 ; i < profile_content.length ; i ++) {
        let to_append = {
          system_category: profile_content[i].system_category,
          display_name: profile_content[i].display_name,
          description: profile_content[i].description,
          first: profile_content[i].first,
          second: profile_content[i].second,
          third: profile_content[i].third
        }
        if (profile[profile_content[i].system_category] == null) {
          profile[profile_content[i].system_category] = [to_append];
        }
        else profile[profile_content[i].system_category].push(to_append);
      }


      //publications
      const publications_content = contents[1];
      let publications = {};
      for (let i = 0 ; i < publications_content.length ; i ++) {
        let to_append = {
          system_category: publications_content[i].system_category,
          display_name: publications_content[i].display_name,
          description: publications_content[i].description,
          title: publications_content[i].title,
          index: publications_content[i].index,
          file_path: publications_content[i].file_path
        }
        if (publications[publications_content[i].system_category] == null) {
          publications[publications_content[i].system_category] = [to_append];
        }
        else publications[publications_content[i].system_category].push(to_append);
      }
     
      //projects
      const projects_content = contents[2];
      let projects = {};
      for (let i = 0 ; i < projects_content.length ; i ++) {
        let to_append = {
          system_category: projects_content[i].system_category,
          display_name: projects_content[i].display_name,
          description: projects_content[i].description,
          department: projects_content[i].department,
          title: projects_content[i].title
        }
        if (projects[projects_content[i].system_category] == null) {
          projects[projects_content[i].system_category] = [to_append];
        }
        else projects[projects_content[i].system_category].push(to_append);
      }
     
      //team
      const team_content = contents[3];
      let team = {};
      for (let i = 0 ; i < team_content.length ; i ++) {
        let to_append = {
          system_category: team_content[i].system_category,
          display_name: team_content[i].display_name,
          description: team_content[i].description,
          name: team_content[i].name
        }
        if (team[team_content[i].system_category] == null) {
          team[team_content[i].system_category] = [to_append];
        }
        else team[team_content[i].system_category].push(to_append);
      }
      
      //services
      const services_content = contents[4];
      let services = {};
      for (let i = 0 ; i < services_content.length ; i ++) {
        let to_append = {
          system_category: services_content[i].system_category,
          display_name: services_content[i].display_name,
          description: services_content[i].description,
          location: services_content[i].location,
          service: services_content[i].service
        }
        if (services[services_content[i].system_category] == null) {
          services[services_content[i].system_category] = [to_append];
        }
        else services[services_content[i].system_category].push(to_append);
      }
      

      const profile_zh_content = contents[5];
      let profile_zh = {};
      for (let i = 0 ; i < profile_content.length ; i ++) {
        let to_append = {
          system_category: profile_zh_content[i].system_category,
          display_name: profile_zh_content[i].display_name,
          description: profile_zh_content[i].description,
          first: profile_zh_content[i].first,
          second: profile_zh_content[i].second,
          third: profile_zh_content[i].third
        }
        if (profile_zh[profile_zh_content[i].system_category] == null) {
          profile_zh[profile_zh_content[i].system_category] = [to_append];
        }
        else profile_zh[profile_zh_content[i].system_category].push(to_append);
      }

      const publications_zh_content = contents[6];
      let publications_zh = {};
      for (let i = 0 ; i < publications_zh_content.length ; i ++) {
        let to_append = {
          system_category: publications_zh_content[i].system_category,
          display_name: publications_zh_content[i].display_name,
          description: publications_zh_content[i].description,
          title: publications_zh_content[i].title,
          index: publications_zh_content[i].index,
          file_path: publications_zh_content[i].file_path
        }
        if (publications_zh[publications_zh_content[i].system_category] == null) {
          publications_zh[publications_zh_content[i].system_category] = [to_append];
        }
        else publications_zh[publications_zh_content[i].system_category].push(to_append);
      }

      const projects_zh_content = contents[7];
      let projects_zh = {};
      for (let i = 0 ; i < projects_zh_content.length ; i ++) {
        let to_append = {
          system_category: projects_zh_content[i].system_category,
          display_name: projects_zh_content[i].display_name,
          description: projects_zh_content[i].description,
          department: projects_zh_content[i].department,
          title: projects_zh_content[i].title
        }
        if (projects_zh[projects_zh_content[i].system_category] == null) {
          projects_zh[projects_zh_content[i].system_category] = [to_append];
        }
        else projects_zh[projects_zh_content[i].system_category].push(to_append);
      }

      const team_zh_content = contents[8];
      let team_zh = {};
      for (let i = 0 ; i < team_zh_content.length ; i ++) {
        let to_append = {
          system_category: team_zh_content[i].system_category,
          display_name: team_zh_content[i].display_name,
          description: team_zh_content[i].description,
          name: team_zh_content[i].name
        }
        if (team_zh[team_zh_content[i].system_category] == null) {
          team_zh[team_zh_content[i].system_category] = [to_append];
        }
        else team_zh[team_zh_content[i].system_category].push(to_append);
      }

      const services_zh_content = contents[9];
      let services_zh = {};
      for (let i = 0 ; i < services_zh_content.length ; i ++) {
        let to_append = {
          system_category: services_zh_content[i].system_category,
          display_name: services_zh_content[i].display_name,
          description: services_zh_content[i].description,
          location: services_zh_content[i].location,
          service: services_zh_content[i].service
        }
        if (services_zh[services_zh_content[i].system_category] == null) {
          services_zh[services_zh_content[i].system_category] = [to_append];
        }
        else services_zh[services_zh_content[i].system_category].push(to_append);
      }


      this.setState({
        Profile: true,
        profile_content: profile,
        publications_content: publications,
        projects_content: projects,
        team_content: team,
        services_content: services,
        profile_zh_content: profile_zh,
        publications_zh_content: publications_zh,
        projects_zh_content: projects_zh,
        team_zh_content: team_zh,
        services_zh_content: services_zh
      });
     
    })

    .catch(function(err) {
        console.log(err.message);
        console.log(err.stack);
    });
    
  }
  

  

  falseState = () => {
    this.setState({
      Profile: false, Publications: false, Projects: false, Team: false, Services: false
    })

    document.getElementById('Profile').classList.remove('navbar-click');
    document.getElementById('Publications').classList.remove('navbar-click');
    document.getElementById('Projects').classList.remove('navbar-click');
    document.getElementById('Team').classList.remove('navbar-click');
    document.getElementById('Services').classList.remove('navbar-click');
  }

  mobileShowDropdown = () => {
    if (document.getElementById('dropdown_list_mobile').style.display === 'block' ) {
      document.getElementById('dropdown_list_mobile').style.display = 'none';
    }
    else document.getElementById('dropdown_list_mobile').style.display = 'block';

  }

  mobileShowDetailDropdown = () => {
    if (document.getElementById('dropdown_detail_mobile').style.display === 'block') {
      document.getElementById('dropdown_detail_mobile').style.display = 'none';
      document.getElementById('dropdown_list_mobile').style.display = 'none';
    }
    else {
      document.getElementById('dropdown_detail_mobile').style.display = 'block';
      document.getElementById('dropdown_list_mobile').style.display = 'none';
    }
  }

  mobileChangePage = (e) => {
    let clicked = e.target.innerHTML;
    if (clicked === 'Profile' 
      || clicked === 'Publications' 
      || clicked === 'Projects' 
      || clicked === 'Team' 
      || clicked === 'Services') {
      this.falseState();
      document.getElementById('dropdown_list_mobile').style.display = 'none';
      this.setState({current_page: clicked});
      switch (clicked) {
        case 'Profile':
          this.setState({Profile: true});
          break;
        case 'Publications':
          this.setState({Publications: true});
          break;
        case 'Projects':
          this.setState({Projects: true});
          break;
        case 'Team':
          this.setState({Team: true});
          break;
        case 'Services':
          this.setState({Services: true});
          break;
        default:
          break;
      }
    }
  }

  changeToEngPage = () => {
    this.setState({language: 'English'});
  }
  changeToZhPage = () => {
    this.setState({language: 'Chinese'});
  }

  changePage = (e) => {
    const clicked = e.target.parentElement.id;
    if (clicked === 'Profile'
      || clicked === 'Publications'
      || clicked === 'Projects'
      || clicked === 'Team'
      || clicked === 'Services') {
        this.falseState();
        document.getElementById(clicked).classList.add('navbar-click');
        switch (clicked) {
          case 'Profile':
            this.setState({Profile: true});
            break;
          case 'Publications':
            this.setState({Publications: true});
            break;
          case 'Projects':
            this.setState({Projects: true});
            break;
          case 'Team':
            this.setState({Team: true});
            break;
          case 'Services':
            this.setState({Services: true});
            break;
          default:
            break;
        }
      }
    document.getElementById('content').scrollTo(0,0);
  }

  rwd_adjust = () => {
    console.log('chekc');
  }


  render() {

    
    return (
      <div className="App">
      <div id = 'engPage'>
        <header className="App-header">
          <div>
            <TitleAndDropdown 
              id = 'dropdown'
              language = {this.state.language}
              changePage = {this.changePage}
            />
            
            <DropdownMobile 
              id = 'dropdown_mobile' 
              showDetailDropdown = {this.mobileShowDetailDropdown}
              showDropdown = {this.mobileShowDropdown} 
              changePage = {this.mobileChangePage} 
              current_page = {this.state.current_page}
            />
            <center id = 'seperation_line'><hr/></center>
          </div>
        </header>
       

        <div className = 'content' id = 'content'>
          {
            this.state.Profile ? 
            <Profile 
              language = {this.state.language}
              onchange = {this.rwd_adjust}
              contents = {this.state.profile_content} 
              contents_zh = {this.state.profile_zh_content}
            /> : null
          }
          {
            this.state.Publications ? 
            <Publications 
              language = {this.state.language}
              contents = {this.state.publications_content}
              contents_zh = {this.state.publications_zh_content}
            /> : null
          }
          {
            this.state.Projects ? 
            <Projects 
              language = {this.state.language}
              contents = {this.state.projects_content} 
              contents_zh = {this.state.projects_zh_content}
            /> : null
          }
          {
            this.state.Team ? 
            <Team 
              language = {this.state.language}
              contents = {this.state.team_content} 
              contents_zh = {this.state.team_zh_content}
            /> : null
          }
          {
            this.state.Services ? 
            <Services 
              language = {this.state.language}
              contents = {this.state.services_content} 
              contents_zh = {this.state.services_zh_content}
            /> : null
          }
          
        </div> 
        
          <div className = 'footer'>
          <center>
            {this.state.language === 'English' ?
              <p>Tzu-Bin Lin / National Taiwan Normal University</p>
              : <p>林子斌 / 國立臺灣師範大學</p>
            }
            
            <a href="mailto: tzubin_lin@ntnu.edu.tw, odin.london@gmail.com">
              <img src = './pics/email-red.png' alt = 'email'/>
            </a>

            <a href="tel:+886-02-7734-3893">
              <img src = './pics/phone-red.png' alt = 'phone'/>
            </a>
            </center>
            <div className="switch">
              <a className = "deactive-tag" onClick = {this.changeToZhPage}>&nbsp;&nbsp;中文&nbsp;&nbsp;</a>
              <a className = "active-tag" onClick = {this.changeToEngPage}>English</a>
            </div>
          </div>
        </div>





        

      </div>
    );
  }
}

export default App;
export {profile_intro_test};
