import React from 'react';
import ProjectGenerator from './ProjectGenerator/ProjectGenerator';
import './Projects.css';

const Projects = (props) => {
	let categories = [];
	let to_render = [];
	let content = [];
	if(props.language === 'English') content = props.contents;
	else content = props.contents_zh;

	for (let item in content) {
		categories.push(item);
	}
	for (let i = 0 ; i < categories.length ; i ++) {
		to_render.push(<h3>{content[categories[i]][0].display_name}</h3>);
		to_render.push(<ProjectGenerator projects = {content[categories[i]]}/>);
	}

	return (
		<div key = 'Projects_table'>
			<div className = 'column big left extended'>

				{to_render}				

			</div>
			<div className = 'column small right static_others'>
				<img className = 'photo' src = './pics/projects_sidephoto.png' alt = 'error'/>
			</div>
		</div>
	);

}

export default Projects;