import React from 'react';
import './ProfileInfo.css';


const ProfileInfo = (props) => {

	let caption = props.caption;
	return(
		<div className = 'info'>
			<img src = './pics/2018_TBL.jpg' alt = 'error'/>
			<div className = 'info_pic' id = 'info_pic'>
				<img src = './pics/logo.gif' alt = 'logo'/>
				<img src = './pics/email.png' alt = 'e-mail'/>
				<img src = './pics/phone.png' alt = 'phone'/>
			</div>
			<div className = 'info_word'>
				<p>{caption[0].first}</p>
				<p>{caption[1].first}<br />{caption[1].second}</p>
				<p><br/>{caption[2].first}</p>
			</div>
		</div>
	);
}

export default ProfileInfo;